import React from "react";
// @material-ui/core components
import Grid from "@material-ui/core/Grid";
import Box from '@material-ui/core/Box';
import Skeleton from '@material-ui/lab/Skeleton';
// import FormHelperText from '@material-ui/core/FormHelperText';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormGroup from '@material-ui/core/FormGroup';
import Checkbox from '@material-ui/core/Checkbox';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';

// dashboard components
import GridItem from "components/Grid/GridItem.js";
import SingleRow from "components/Grid/SingleRow.js";
import InputList from "components/CustomInput/InputList";
import CustomDialog from "components/Dialog/CustomDialog.js";
import {
  getAllComputePools, searchDiskImages, createInstance, getSystemStatus,
  getInstanceConfig, querySystemTemplates, searchSecurityPolicyGroups
} from 'nano_api.js';

const i18n = {
  'en': {
    title: 'Create Instance',
    name: 'Instance Name',
    resourcePool: 'Resource Pool',
    core: 'Core',
    memory: 'Memory',
    systemDisk: 'System Disk Size',
    dataDisk: 'Data Disk Size',
    autoStartup: 'Automatic Startup',
    systemVersion: 'System Version',
    sourceImage: 'Source Image',
    blankSystem: 'Blank System',
    qos: 'QoS Options (Optional)',
    cpuPriority: 'CPU Priority',
    iops: 'IOPS',
    outbound: 'Outband Bandwidth',
    inbound: 'Inbound Bandwidth',
    noDataDisk: "Don't use data disk",
    noLimit: 'No Limit',
    cpuPriorityHigh: 'High',
    cpuPriorityMedium: 'Medium',
    cpuPriorityLow: 'Low',
    modules: 'Pre-Installed Modules',
    adminName: 'Admin Name',
    adminPassword: 'Admin Password',
    blankHelper: 'Leave blank to generate',
    dataPath: 'Data Path',
    off: 'Off',
    on: 'On',
    ciOptions: 'Cloud Init Options',
    securityPolicy: 'Security Policy',
    cancel: 'Cancel',
    confirm: 'Confirm',
  },
  'cn': {
    title: '',
    name: '',
    resourcePool: '',
    core: '',
    memory: '',
    systemDisk: '',
    dataDisk: '',
    autoStartup: '',
    systemVersion: '',
    sourceImage: '',
    blankSystem: '',
    qos: 'QoS ()',
    cpuPriority: 'CPU',
    iops: '',
    outbound: '',
    inbound: '',
    noDataDisk: "",
    noLimit: '',
    cpuPriorityHigh: '',
    cpuPriorityMedium: '',
    cpuPriorityLow: '',
    modules: '',
    adminName: '',
    adminPassword: '',
    blankHelper: '',
    dataPath: '',
    off: '',
    on: '',
    ciOptions: 'Cloud Init',
    securityPolicy: '',
    cancel: '',
    confirm: '',
  },
};

export default function CreateDialog(props) {
  const defaultMaxCores = 16;
  const defaultMaxMemory = 24;
  const defaultMaxDisk = 32;
  const defaultOption = '__default';
  const ciModuleName = 'cloud-init';
  const checkInterval = 1000;
  const { lang, open, onSuccess, onCancel } = props;

  const defaultValues = {
    name: '',
    pool: '',
    cores: (1).toString(),
    memory: (1 << 30).toString(), //1 G
    system_disk: 5,
    data_disk: 0,
    auto_start: false,
    system_template: '',
    from_image: defaultOption,
    security_policy: '',
    modules: new Map(),
    module_cloud_init_admin_name: 'root',
    module_cloud_init_admin_password: '',
    module_cloud_init_data_path: '/opt/data',
    priority: 'medium',
    iops: 0,
    inbound: 0,
    outbound: 0,
  };
  const [initialed, setInitialed] = React.useState(false);
  const [creating, setCreating] = React.useState(false);
  const [progress, setProgress] = React.useState(0);
  const [operatable, setOperatable] = React.useState(true);
  const [prompt, setPrompt] = React.useState('');
  const [mounted, setMounted] = React.useState(false);
  const [request, setRequest] = React.useState(defaultValues);
  const [options, setOptions] = React.useState({
    pools: [],
    images: [],
    versions: [],
    policies: [],
  });
  const [maxCores, setMaxCores] = React.useState(defaultMaxCores);
  const [maxMemory, setMaxMemory] = React.useState(defaultMaxMemory);
  const [maxDisk, setMaxDisk] = React.useState(defaultMaxDisk);
  const [coreValue, setCoreValue] = React.useState(0);
  const [memoryTick, setMemoryTick] = React.useState(1);
  const texts = i18n[lang];
  const title = texts.title;
  const onCreateFail = React.useCallback(msg => {
    if (!mounted) {
      return;
    }
    setOperatable(true);
    setPrompt(msg);
  }, [mounted]);

  const resetDialog = () => {
    setPrompt('');
    setRequest(defaultValues);
    setInitialed(false);
    setCreating(false);
    setProgress(0);
  }

  const closeDialog = () => {
    resetDialog();
    onCancel();
  }

  const onCreateSuccess = result => {
    if (!mounted) {
      return;
    }
    setOperatable(true);
    resetDialog();
    onSuccess(result.id);
  }

  const onCreateAccept = instanceID => {
    if (!mounted) {
      return;
    }
    setCreating(true);
    setProgress(0);
    setTimeout(() => {
      checkCreatingProgress(instanceID)
    }, checkInterval);
  }

  const checkCreatingProgress = instanceID => {
    if (!mounted) {
      return;
    }
    const onCreating = progress => {
      if (!mounted) {
        return;
      }
      setProgress(progress);
      setTimeout(() => {
        checkCreatingProgress(instanceID)
      }, checkInterval);
    }
    getInstanceConfig(instanceID, onCreateSuccess, onCreateFail, onCreating);
  }

  const handleConfirm = () => {
    setPrompt('');
    setOperatable(false);
    if (!request.name) {
      onCreateFail('instance name required');
      return;
    }
    if (!request.pool) {
      onCreateFail('must specify target pool');
      return;
    }
    var cores = Number(request.cores);
    if (Number.isNaN(cores)) {
      onCreateFail('invalid cores: ' + request.cores);
      return;
    }
    var memory = Number(request.memory);
    if (Number.isNaN(memory)) {
      onCreateFail('invalid memory: ' + request.memory);
      return;
    }
    const GiB = 1 << 30;
    var disks = [request.system_disk * GiB];
    if (0 !== request.data_disk) {
      disks.push(request.data_disk * GiB);
    }
    var systemVersion = request.system_template;
    let fromImage;
    if (defaultOption === request.from_image) {
      fromImage = '';
    } else {
      fromImage = request.from_image;
    }
    var modules = [];
    var ciEnabled = false;
    request.modules.forEach((checked, name) => {
      if (checked) {
        modules.push(name);
        if (ciModuleName === name) {
          ciEnabled = true;
        }
      }
    });
    var cloudInit = null;
    if (ciEnabled) {
      cloudInit = {
        admin_name: request.module_cloud_init_admin_name,
        admin_secret: request.module_cloud_init_admin_password,
        data_path: request.module_cloud_init_data_path,
      };
    }
    const Mbit = 1 << (20 - 3);
    var qos = {
      cpu_priority: request.priority,
      write_iops: request.iops,
      read_iops: request.iops,
      receive_speed: request.inbound * Mbit,
      send_speed: request.outbound * Mbit,
    };
    createInstance(request.name, request.pool, cores, memory, disks,
      request.auto_start, fromImage, systemVersion, modules,
      cloudInit, qos, request.security_policy, onCreateAccept,
      onCreateSuccess, onCreateFail);
  }

  const handleRequestPropsChanged = name => e => {
    if (!mounted) {
      return;
    }
    var value = e.target.value
    setRequest(previous => ({
      ...previous,
      [name]: value,
    }));
  };

  const handleSliderValueChanged = name => (e, value) => {
    if (!mounted) {
      return;
    }
    setRequest(previous => ({
      ...previous,
      [name]: value,
    }));
  };

  const handleCheckedValueChanged = name => e => {
    if (!mounted) {
      return;
    }
    var value = e.target.checked
    setRequest(previous => ({
      ...previous,
      [name]: value,
    }));
  };

  const handleCheckedGroupChanged = (groupName, propertyName) => e => {
    if (!mounted) {
      return;
    }
    var checked = e.target.checked
    setRequest(previous => ({
      ...previous,
      [groupName]: previous[groupName].set(propertyName, checked),
    }));
  };

  React.useEffect(() => {
    if (!open) {
      return;
    }
    // var session = getLoggedSession();
    // if (null === session){
    //   onCreateFail('session expired');
    //   return;
    // }

    var poolOptions = [];
    var imageOptions = [{
      label: texts.blankSystem,
      value: defaultOption,
    }];
    var templateOptions = [];
    var securityPolicies = [];

    setMounted(true);
    const onQueryPoliciesSuccess = datalist => {
      if (!mounted) {
        return;
      }
      datalist.forEach(({ id, name }) => {
        securityPolicies.push({
          label: name,
          value: id,
        });
      });
      setOptions({
        pools: poolOptions,
        images: imageOptions,
        versions: templateOptions,
        policies: securityPolicies,
      });
      setInitialed(true);
    }
    const onQueryTemplateSuccess = dataList => {
      if (!mounted) {
        return;
      }
      dataList.forEach(({ id, name }) => {
        templateOptions.push({
          label: name,
          value: id,
        });
      });
      searchSecurityPolicyGroups('', '', true, true,
        onQueryPoliciesSuccess, onCreateFail);
    }

    const onQueryImageSuccess = dataList => {
      if (!mounted) {
        return;
      }
      dataList.forEach(({ name, id }) => {
        imageOptions.push({
          label: name,
          value: id,
        });
      })

      querySystemTemplates(onQueryTemplateSuccess, onCreateFail);
    };

    const onQueryPoolSuccess = dataList => {
      if (!mounted) {
        return;
      }
      dataList.forEach(({ name }) => {
        poolOptions.push({
          label: name,
          value: name,
        });
      })

      searchDiskImages(onQueryImageSuccess, onCreateFail);
    };

    const onGetSystemStatusSuccess = status => {
      if (!mounted) {
        return;
      }
      if (status.max_cores && status.max_cores > 0) {
        setMaxCores(status.max_cores);
      }
      if (status.max_memory && status.max_memory > 0) {
        setMaxMemory(status.max_memory);
      }
      if (status.max_disk && status.max_disk > 0) {
        setMaxDisk(status.max_disk);
      }
      getAllComputePools(onQueryPoolSuccess, onCreateFail);
    }

    getSystemStatus(onGetSystemStatusSuccess, onCreateFail);

    return () => {
      setMounted(false);
    }
  }, [mounted, open, texts.blankSystem, onCreateFail]);

  //begin render
  var buttons = [{
    color: 'transparent',
    label: texts.cancel,
    onClick: closeDialog,
  }];
  let content;
  if (!initialed) {
    content = <Skeleton variant="rect" style={{ height: '10rem' }} />;
  } else if (creating) {
    content = (
      <Grid container>
        <SingleRow>
          <GridItem xs={12}>
            <LinearProgress variant="determinate" value={progress} />
          </GridItem>
        </SingleRow>
        <SingleRow>
          <GridItem xs={12}>
            <Typography align="center">
              {progress.toFixed(2) + '%'}
            </Typography>
          </GridItem>
        </SingleRow>
      </Grid>
    )
  } else {
    // new core component
    const coreLabel = value => {
      let cores = 2 ** value;
      if (cores > maxCores) {
        cores = maxCores;
      }
      return cores.toString();
    }

    const onCoreChanged = (e, value) => {
      if (!mounted) {
        return;
      }
      setCoreValue(value);
      let cores = 2 ** value;
      if (cores > maxCores) {
        cores = maxCores;
      }
      setRequest(previous => ({
        ...previous,
        cores: cores,
      }));
    };
    let maxCoreRange = Math.ceil(Math.sqrt(maxCores));
    let minCoreRange = 0;
    let coreMarks = [];
    for (let value = minCoreRange + 1; value <= maxCoreRange; ++value) {
      let cores = 2 ** value;
      if (cores > maxCores) {
        cores = maxCores;
      }
      coreMarks.push({
        value: value,
        label: cores.toString(),
      });
    }

    let coreComponent = {
      type: 'slider',
      label: texts.core + ` - ${request.cores}`,
      onChange: onCoreChanged,
      valueLabelFormat: coreLabel,
      value: coreValue,
      oneRow: true,
      maxStep: maxCoreRange,
      minStep: minCoreRange,
      step: 1,
      marks: coreMarks,
      xs: 12,
      sm: 6,
      md: 4,
    };

    // new memory component
    const memoryBase = 512;
    const MiB = 1 << 20;
    const GiB = 1 << 30;

    const displayMemory = memory => {
      let label;
      if (memory >= GiB) {
        label = memory / GiB + ' GB';
      } else {
        label = memory / MiB + ' MB';
      }
      return label;
    }

    const onMemoryChanged = (e, value) => {
      if (!mounted) {
        return;
      }
      setMemoryTick(value);
      let memory = 2 ** value * memoryBase * MiB;
      if (memory > maxMemory * GiB) {
        memory = maxMemory * GiB;
      }
      setRequest(previous => ({
        ...previous,
        memory: memory,
      }));
    };
    let maxMemoryRange = Math.ceil(Math.sqrt(maxMemory));
    let minMemoryRange = 0;
    let memoryMarks = [];
    for (let value = minMemoryRange + 1; value <= maxMemoryRange; ++value) {
      let memory = 2 ** value * memoryBase * MiB;
      if (memory > maxMemory * GiB) {
        memory = maxMemory * GiB;
      }
      memoryMarks.push({
        value: value,
        label: displayMemory(memory),
      });
    }
    let memoryLabel = texts.memory + ` - ${displayMemory(request.memory)}`;
    let memoryComponent = {
      type: 'slider',
      label: memoryLabel,
      onChange: onMemoryChanged,
      valueLabelDisplay: 'off',
      value: memoryTick,
      oneRow: true,
      maxStep: maxMemoryRange,
      minStep: minMemoryRange,
      step: 1,
      marks: memoryMarks,
      xs: 12,
      sm: 8,
      md: 6,
    };

    //system disk slider
    let systemDiskSlider;
    {
      const step = 1;
      const minRange = 5;
      const maxRange = maxDisk;
      let midRange = maxRange >> 1;
      const markValues = [minRange, midRange, maxRange];
      var systemMarks = [];
      markValues.forEach(value => {
        systemMarks.push({
          value: value,
          label: value + ' GB',
        })
      });
      systemDiskSlider = {
        type: 'slider',
        label: texts.systemDisk + ` - ${request.system_disk} GB`,
        onChange: handleSliderValueChanged('system_disk'),
        value: request.system_disk,
        oneRow: true,
        maxStep: maxRange,
        minStep: minRange,
        step: step,
        marks: systemMarks,
        xs: 12,
        sm: 6,
        md: 4,
      };
    }
    //disk slider
    let dataDiskSlider;
    {
      const minRange = 0;
      const step = 2;
      const maxRange = maxDisk;
      let midRange = maxRange >> 1;
      const markValues = [minRange, midRange, maxRange];
      let dataMarks = [];
      markValues.forEach(value => {
        dataMarks.push({
          value: value,
          label: value + ' GB',
        })
      });
      let dataDiskLabel;
      if (0 === request.data_disk) {
        dataDiskLabel = texts.dataDisk + ' - ' + texts.noDataDisk;
      } else {
        dataDiskLabel = texts.dataDisk + ` - ${request.data_disk} GB`;
      }
      dataDiskSlider = {
        type: 'slider',
        label: dataDiskLabel,
        onChange: handleSliderValueChanged('data_disk'),
        value: request.data_disk,
        oneRow: true,
        maxStep: maxRange,
        minStep: minRange,
        step: step,
        marks: dataMarks,
        xs: 12,
        sm: 6,
        md: 4,
      };
    }

    let moduleOption;
    if (request.system_template && defaultOption !== request.from_image) {
      var modules = [{
        value: 'qemu',
        label: 'QEMU-Guest-Agent',
      },
      {
        value: ciModuleName,
        label: 'CloudInit',
      }
      ];
      let ciOptions;
      if (request.modules.get(ciModuleName)) {
        const ciComponents = [
          {
            type: "text",
            label: texts.adminName,
            onChange: handleRequestPropsChanged('module_cloud_init_admin_name'),
            value: request.module_cloud_init_admin_name,
            oneRow: true,
            xs: 12,
          },
          {
            type: "text",
            label: texts.adminPassword,
            onChange: handleRequestPropsChanged('module_cloud_init_admin_password'),
            helper: texts.blankHelper,
            oneRow: true,
            xs: 12,
          },
          {
            type: "text",
            label: texts.dataPath,
            onChange: handleRequestPropsChanged('module_cloud_init_data_path'),
            value: request.module_cloud_init_data_path,
            disabled: true,
            oneRow: true,
            xs: 12,
          },
        ];
        //ci checked
        ciOptions = (
          <GridItem xs={12} sm={8} md={6}>
            <FormLabel component="legend">{texts.ciOptions}</FormLabel>
            <InputList inputs={ciComponents} />
          </GridItem>
        )
      } else {
        ciOptions = <GridItem />;
      }

      moduleOption = (
        <SingleRow>
          <GridItem xs={12}>
            <Box m={0} pt={2}>
              <FormControl component="fieldset" fullWidth>
                <FormLabel component="legend">{texts.modules}</FormLabel>
                <FormGroup>
                  <Grid container>
                    {
                      modules.map(module => {
                        let checked;
                        if (request.modules.has(module.value)) {
                          checked = request.modules.get(module.value);
                        } else {
                          checked = false;
                        }
                        return (
                          <GridItem xs={12} sm={6} md={4} key={module.value}>
                            <FormControlLabel
                              control={<Checkbox checked={checked} onChange={handleCheckedGroupChanged('modules', module.value)} value={module.value} />}
                              label={module.label}
                            />
                          </GridItem>
                        );
                      })
                    }
                  </Grid>
                </FormGroup>
              </FormControl>
            </Box>
          </GridItem>
          {ciOptions}
        </SingleRow>
      )

    } else {
      moduleOption = <GridItem />;
    }

    var inputComponents = [
      {
        type: "text",
        label: texts.name,
        onChange: handleRequestPropsChanged('name'),
        value: request.name,
        oneRow: true,
        required: true,
        xs: 12,
        sm: 6,
        md: 4,
      },
      {
        type: "select",
        label: texts.resourcePool,
        onChange: handleRequestPropsChanged('pool'),
        value: request.pool,
        oneRow: true,
        options: options.pools,
        required: true,
        xs: 10,
        sm: 4,
        md: 3,
      },
      coreComponent,
      memoryComponent,
      systemDiskSlider,
      dataDiskSlider,
      {
        type: "select",
        label: texts.sourceImage,
        onChange: handleRequestPropsChanged('from_image'),
        value: request.from_image,
        oneRow: true,
        options: options.images,
        xs: 10,
        sm: 6,
        md: 5,
      },
      {
        type: "select",
        label: texts.systemVersion,
        onChange: handleRequestPropsChanged('system_template'),
        value: request.system_template,
        oneRow: true,
        options: options.versions,
        xs: 10,
        sm: 5,
        md: 4,
      },
      {
        type: "switch",
        label: texts.autoStartup,
        onChange: handleCheckedValueChanged('auto_start'),
        value: request.auto_start,
        on: texts.on,
        off: texts.off,
        oneRow: true,
        xs: 8,
        sm: 6,
        md: 4,
      },
    ];
    if (options.policies && 0 !== options.policies.length) {
      inputComponents.push({
        type: "select",
        label: texts.securityPolicy,
        onChange: handleRequestPropsChanged('security_policy'),
        value: request.security_policy,
        oneRow: true,
        options: options.policies,
        xs: 10,
        sm: 5,
        md: 4,
      })
    }

    const priorityOptions = [
      {
        value: 'high',
        label: texts.cpuPriorityHigh,
      },
      {
        value: 'medium',
        label: texts.cpuPriorityMedium,
      },
      {
        value: 'low',
        label: texts.cpuPriorityLow,
      },
    ]

    const maxIOPS = 2000;
    const iopsAchor = [0, maxIOPS / 2, maxIOPS];
    let iopsMarks = [];
    iopsAchor.forEach(value => {
      iopsMarks.push({
        value: value,
        label: value.toString(),
      })
    });

    const maxBandwidth = 20;
    const bandwidthAchor = [0, maxBandwidth / 2, maxBandwidth];
    let bandwidthMarks = [];
    bandwidthAchor.forEach(value => {
      bandwidthMarks.push({
        value: value,
        label: 0 === value ? texts.noLimit : value.toString() + ' Mbit/s',
      })
    });

    let iopsLabel, inboundLabel, outboundLabel;
    if (0 === request.iops) {
      iopsLabel = texts.iops + ' - ' + texts.noLimit;
    } else{
      if ('cn' === lang) {
        iopsLabel = texts.iops + ' - ' + request.iops + '/';
      }else{
        iopsLabel = texts.iops + ' - ' + request.iops + ' Operates/Second';
      }
    }
    if (0 === request.inbound) {
      inboundLabel = texts.inbound + ' - ' + texts.noLimit;
    } else{
      inboundLabel = texts.inbound + ' - ' + request.inbound + ' Mbit/s';
    }
    if (0 === request.outbound) {
      outboundLabel = texts.outbound + ' - ' + texts.noLimit;
    } else{
      outboundLabel = texts.outbound + ' - ' + request.outbound + ' Mbit/s';
    }

    const qosComponents = [
      {
        type: "radio",
        label: texts.cpuPriority,
        onChange: handleRequestPropsChanged('priority'),
        value: request.priority,
        oneRow: true,
        options: priorityOptions,
        xs: 12,
      },
      {
        type: 'slider',
        label: iopsLabel,
        onChange: handleSliderValueChanged('iops'),
        value: request.iops,
        oneRow: true,
        maxStep: maxIOPS,
        minStep: 0,
        step: 10,
        marks: iopsMarks,
        xs: 12,
      },
      {
        type: 'slider',
        label: inboundLabel,
        onChange: handleSliderValueChanged('inbound'),
        value: request.inbound,
        oneRow: true,
        maxStep: maxBandwidth,
        minStep: 0,
        step: 2,
        marks: bandwidthMarks,
        xs: 12,
      },
      {
        type: 'slider',
        label: outboundLabel,
        onChange: handleSliderValueChanged('outbound'),
        value: request.outbound,
        oneRow: true,
        maxStep: maxBandwidth,
        minStep: 0,
        step: 2,
        marks: bandwidthMarks,
        xs: 12,
      },
    ];

    content = (
      <Grid container>
        <Box m={1} pt={2}>
          <InputList inputs={inputComponents} />
        </Box>
        {moduleOption}
        <SingleRow>
          <GridItem xs={12} sm={9} md={7}>
            <Box m={0} pb={2}>
              <Accordion>
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                >
                  {texts.qos}
                </AccordionSummary>
                <AccordionDetails>
                  <Box m={1} pt={2}>
                    <InputList inputs={qosComponents} />
                  </Box>
                </AccordionDetails>
              </Accordion>
            </Box>
          </GridItem>
        </SingleRow>
      </Grid>
    );
    buttons.push({
      color: 'info',
      label: texts.confirm,
      onClick: handleConfirm,
    });
  }
  return <CustomDialog size='md' open={open} prompt={prompt} promptPosition="top"
    hideBackdrop title={title} buttons={buttons} content={content} operatable={operatable} />;
};
